@include font-face('BonnetGrotesqueNr', 100, normal, "./../assets/fonts/bonnet-grotesque-nr/BonnetGrotesqueNr-Thin.ttf");
@include font-face('BonnetGrotesqueNr', 300, normal, "./../assets/fonts/bonnet-grotesque-nr/BonnetGrotesqueNr-Light.ttf");
@include font-face('BonnetGrotesqueNr', 400, normal, "./../assets/fonts/bonnet-grotesque-nr/BonnetGrotesqueNr-Regular.ttf");
@include font-face('BonnetGrotesqueNr', 500, normal, "./../assets/fonts/bonnet-grotesque-nr/BonnetGrotesqueNr-Medium.ttf");
@include font-face('BonnetGrotesqueNr', 700, normal, "./../assets/fonts/bonnet-grotesque-nr/BonnetGrotesqueNr-Bold.ttf");

:root {
  --BonnetGrotesqueNr: 'BonnetGrotesqueNr';
  --BonnetGrotesqueNr-Thin: 100;
  --BonnetGrotesqueNr-Light: 300;
  --BonnetGrotesqueNr-Regular: 400;
  --BonnetGrotesqueNr-Medium: 500;
  --BonnetGrotesqueNr-Bold: 700;
}