*,
*::before,
*::after {
  margin: 0;
  padding: 0;

  font-family: inherit;
  font-weight: inherit;
  font-size: inherit;
  color: inherit;

  box-sizing: border-box;

  text-align: left;
}

button {
  background-color: transparent;

  border: none;

  cursor: pointer;
}

a {
  text-decoration: none;
  cursor: pointer;
}

ul,
ol,
menu {
  list-style: none;
}

li {
  list-style: none;
}