@include font-face('Inter', 100, normal, "./../assets/fonts/Inter/Inter_18pt-Thin.ttf");
@include font-face('Inter', 100, italic, "./../assets/fonts/Inter/Inter_18pt-ThinItalic.ttf");

@include font-face('Inter', 200, normal, "./../assets/fonts/Inter/Inter_18pt-ExtraLight.ttf");
@include font-face('Inter', 200, italic, "./../assets/fonts/Inter/Inter_18pt-ExtraLightItalic.ttf");

@include font-face('Inter', 300, normal, "./../assets/fonts/Inter/Inter_18pt-Light.ttf");
@include font-face('Inter', 300, italic, "./../assets/fonts/Inter/Inter_18pt-LightItalic.ttf");

@include font-face('Inter', 400, normal, "./../assets/fonts/Inter/Inter_18pt-Regular.ttf");
@include font-face('Inter', 400, italic, "./../assets/fonts/Inter/Inter_18pt-Italic.ttf");

@include font-face('Inter', 500, normal, "./../assets/fonts/Inter/Inter_18pt-Medium.ttf");
@include font-face('Inter', 500, italic, "./../assets/fonts/Inter/Inter_18pt-MediumItalic.ttf");

@include font-face('Inter', 600, normal, "./../assets/fonts/Inter/Inter_18pt-SemiBold.ttf");
@include font-face('Inter', 600, italic, "./../assets/fonts/Inter/Inter_18pt-SemiBoldItalic.ttf");

@include font-face('Inter', 700, normal, "./../assets/fonts/Inter/Inter_18pt-Bold.ttf");
@include font-face('Inter', 700, italic, "./../assets/fonts/Inter/Inter_18pt-BoldItalic.ttf");

@include font-face('Inter', 800, normal, "./../assets/fonts/Inter/Inter_18pt-ExtraBold.ttf");
@include font-face('Inter', 800, italic, "./../assets/fonts/Inter/Inter_18pt-ExtraBoldItalic.ttf");

@include font-face('Inter', 900, normal, "./../assets/fonts/Inter/Inter_18pt-Black.ttf");
@include font-face('Inter', 900, italic, "./../assets/fonts/Inter/Inter_18pt-BlackItalic.ttf");

:root {
  --Inter: 'Inter';
  --Inter-Thin: 100;
  --Inter-ExtraLight: 200;
  --Inter-Light: 300;
  --Inter-Regular: 400;
  --Inter-Medium: 500;
  --Inter-SemiBold: 600;
  --Inter-Bold: 700;
  --Inter-ExtraBold: 800;
  --Inter-Black: 900;
}