html {
  font-size: calc((16px / (1889px / 100)) * 1vw);

  @media screen and (max-width: $lg) {
    font-size: 8px;
  }
}

.about-us {}

.about-us-row1 {
  margin-left: calc((361px / (1889px / 100)) * 1%);
  margin-right: calc((183px / (1889px / 100)) * 1%);

  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  gap: 10px 36px;

  width: calc((1344px / (1889px / 100)) * 1%);

  @media screen and (max-width: $lg) {
    margin-left: auto;
    margin-right: auto;

    width: 80%;

    flex-direction: column;
    align-items: center;

    .about-us-row1__right {
      max-width: 50%;
    }
  }

  // .about-us-row1__left

  &__left {
    flex: 1 1;
  }

  // .about-us-row1__title

  &__title {
    font-family: var(--BonnetGrotesqueNr);
    font-weight: var(--BonnetGrotesqueNr-Medium);
    font-size: rem(70);
    color: var(--blocks);
    text-transform: uppercase;
  }

  // .about-us-row1__vertical-line

  &__vertical-line {
    margin-top: rem(26);

    width: rem(98);
    height: rem(5);

    background: var(--accent);
  }

  // .about-us-row1__subtitle

  &__subtitle {
    margin-top: rem(34);
    margin-bottom: rem(18);

    font-family: var(--Inter);
    font-weight: var(--Inter-Regular);
    font-size: rem(25);
    color: var(--blocks);
    line-height: 144%;

    strong {
      font-weight: var(--Inter-Bold);
    }
  }

  // .about-us-row1__right

  &__right {
    flex: 1 1 100px;

    max-width: 683px;
  }

  // .about-us-row1__inner

  &__inner {
    width: 100%;
  }

  // .about-us-row1__image

  &__image {
    width: 100%;
  }
}

.about-us-row2 {
  margin-top: rem(52);
  margin-left: calc((125px / (1889px / 100)) * 1%);
  margin-right: calc((367px / (1889px / 100)) * 1%);

  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px 60px;

  width: calc((1396px / (1889px / 100)) * 1%);

  @media screen and (max-width: $lg) {
    margin-left: auto;
    margin-right: auto;

    width: 80%;

    flex-direction: column;
    align-items: center;

    .about-us-row2__left {
      max-width: 50%;
    }
  }

  // .about-us-row2__left

  &__left {
    flex: 1 1;
  }

  // .about-us-row2__inner

  &__inner {
    width: 100%;
  }

  // .about-us-row2__image

  &__image {
    width: 100%;
  }

  // .about-us-row2__right

  &__right {
    flex: 1 1 100px;

    max-width: 683px;
  }

  // .about-us-row2__title

  &__title {
    font-family: var(--Inter);
    font-weight: var(--Inter-Medium);
    font-size: rem(25);
    color: var(--blocks);
    line-height: 144%;

    strong {
      font-weight: var(--Inter-Bold);
    }
  }

  // .about-us-row2__vertical-line

  &__vertical-line {
    margin-top: rem(26);

    width: rem(98);
    height: rem(5);

    background: var(--accent);
  }

  // .about-us-row2__subtitle

  &__subtitle {
    margin-top: rem(24);

    font-family: var(--Inter);
    font-weight: var(--Inter-Medium);
    font-size: rem(25);
    color: var(--blocks);
    line-height: 144%;
  }
}